type ArrowProps = {
  bottom?: boolean
  left?: boolean
  right?: boolean
  top?: boolean
}

const Arrow = ({
  bottom = false,
  left = false,
  right = false,
  top = false
}: ArrowProps) => {
  let arrow = '‚Üê'
  if (bottom) {
    arrow = '‚Üì'
  }
  if (left) {
    arrow = '‚Üê'
  }
  if (right) {
    arrow = '‚Üí'
  }
  if (top) {
    arrow = '‚Üë'
  }
  return (
    <div>{arrow}</div>
  )
}

export default Arrow
